.App {
  text-align: right;
}
nav{
  background-color: yellow;
  padding:20px;
}
nav h2{
  text-align: left;
}
nav a{
  display: inline-flex;
  font-size: bold;
  margin-right:25px;
  text-decoration: none;

}
nav a:active{
  color:blue;
}
.App div{
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
